import { Component, h, Host, Prop } from '@stencil/core';

export type Template = 'content-container' | 'master-detail' | 'tabset';

/**
 * @category Layout
 * @description A base layout component for structuring page content with flexible sections and responsive behavior. Provides foundation for consistent page layouts.
 *
 * @slot master - Used as the master element when template is master-detail
 * @slot detail - Used as the detail element when template is master-detail
 * @slot tabset-navigation - Used as the navigation element when template is tabset
 * @slot tabset-content - Used as the content element when template is tabset
 * @slot - Content when template is content-container
 */
@Component({
    tag: 'at-layout',
    shadow: false,
})
export class AtLayout {
    /**
     * Template used to display content
     */
    @Prop()
    template: Template = 'master-detail';

    /**
     * If overflow is allowed on the detail in master-detail
     */
    @Prop()
    overflow = true;

    get layoutElement(): any {
        switch (this.template) {
            case 'master-detail':
                return (
                    <div
                        class="flex h-full flex-grow overflow-hidden"
                        data-name="wrapper-master-detail"
                    >
                        <div class="w-sidebar flex flex-col overflow-hidden">
                            <slot name="master" />
                        </div>
                        <div
                            class={`flex flex-grow flex-col ${this.overflow ? 'overflow-auto' : 'overflow-hidden'}`}
                        >
                            <slot name="detail" />
                        </div>
                    </div>
                );
            case 'tabset':
                return (
                    <div
                        class="flex w-full flex-col"
                        data-name="wrapper-tabset"
                    >
                        <slot name="tabset-navigation" />
                        <div class="flex w-full flex-col overflow-auto">
                            <slot name="tabset-content" />
                        </div>
                    </div>
                );
            case 'content-container':
                return (
                    <div
                        class="flex flex-grow justify-center py-64"
                        data-name="wrapper-content-container"
                    >
                        <div class="flex max-w-3xl flex-grow flex-col">
                            <slot />
                        </div>
                    </div>
                );
        }
    }

    render() {
        return <Host>{this.layoutElement}</Host>;
    }
}
